* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Raleway", sans-serif;
}

.App {
  width: 100vw;
  height: 100vw;
}

body {
  margin: 0;
  padding: 0;
  background: radial-gradient(
    47.39% 118.47% at 3.68% -21.35%,
    rgba(255, 219, 19, 0.1) 0%,
    rgba(255, 125, 75, 0.1) 30.4%,
    rgba(231, 130, 247, 0.1) 62.15%,
    rgba(216, 65, 137, 0.1) 100%
  );
  /* border-radius: 24px; */
}


.dashboard,
.proposal {
  background: #faf5f2;
  border: 6px solid rgba(39, 41, 49, 0.07);
  border-radius: 20px;
  max-width: 1180px;
  min-height: 850px;
}

@media screen and (min-width: 992px) {
  .dashboard,
  .proposal {
    margin: 30px 21px 20px 19px;
  }
}

@media screen and (max-width: 991px) and (min-width: 576px) {
  .dashboard,
  .proposal {
    background-color: transparent;
    background: #faf5f2;
    border: 6px solid rgba(39, 41, 49, 0.07);
    border-radius: 20px;
    max-width: 1180px;
    min-height: 850px;
  }
}

@media screen and (max-width: 575px) {
  .dashboard,
  .proposal {
    border: 4px solid rgba(39, 41, 49, 0.07);
    border-left-style: none;
    border-right-style: none;
    border-bottom-style: none;
    background: #faf5f2;
  }
}


.searchField {
  background: #ffffff;
  border-radius: 13px;
  border: 0;
  width: 100%;
  height: 36px;
  padding: 8px 16px;
}

.avatar {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: block;
}

.headerRow {
  margin-top: 30px;
  margin-left: 30px;
}

.logo_colour {
  height: 46px;
  object-fit: contain;
  padding: 0px;
  display: flex;
}

.menu-bars {
  margin-left: 2rem;
  width: auto;
  font-size: 2rem;
  background: none;
  display: flex;
  align-items: center;
  flex: flex-start;
}

.nav_icon {
  display: block;
  flex-grow: 1;
}

@media only screen and (min-width: 992px) {
  .sidebarLogo {
    height: 120px;
    margin-left: 44px;
    width: auto;
  }

  .logoVertical {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: flex-end;
    padding-bottom: 16px;
  }

  .nav_icon {
    display: none;
  }

  .Sidebar {
    height: 100%;
    padding-top:20px ;
    width: auto;
    display: flex;
  }

  .menu-bars {
    display: none;
  }
}

.avatarNav {
  display: none;
}

@media screen and (max-width: 991px) {
  .Sidebar {
    display: none;
  }

  .SidebarList {
    display: none;
  }

  .avatar {
    display: none;
  }

  .avatarNav {
    border-radius: 50%;
    width: 44px;
    height: 44px;
    display: flex;
  }

  .sidebarLogo {
    display: none;
  }


  .logo_colour {
    padding: 5px;
    width: 30px;
    height: 30px;
    margin: 10px;
    line-height: 30px;
    text-align: center;
  }


  .imgNav {
    padding: 5px;
    width: 30px;
    height: 30px;
    margin: 10px;
    line-height: 30px;
    text-align: center;
  }

  .menu-bars {
    padding: 5px;
    width: 30px;
    height: 30px;
    margin: 10px;
    line-height: 30px;
    text-align: center;
  }
}

.SidebarList {
  height: 80px;
  padding: 0;
  width: auto;
}

.SidebarList .row {
  width: 200px;
  height: 50px;
  margin: 20px;
  display: flex;
  flex-direction: row;
  color: #2d3134;
  justify-content: center;
  align-items: center;
}

.SidebarList #active {
  background-color: #fe8862;
  border-radius: 16px;
  color: white;
}

.row #icon {
  flex: 30%;
  display: grid;
  place-items: center;
}

.row #iconActive {
  fill: #fe8862;
  flex: 30%;
  display: grid;
  place-items: center;
  fill: white;
}

.row #title {
  flex: 70%;
}

